import AddStoryPresenter from "../../presenter/add-story-presenter";
import L from "leaflet";

const AddStoryPage = {
  async render() {
    return `
      <h2 class="page-title">Tambah Story Baru</h2>
      <div class="container add-story-content">
        <div id="loading-indicator" class="loading-indicator" style="display: none;">Memuat...</div>
        <div id="status-message" class="status-message" style="display: none;"></div>
        
        <form id="addStoryForm" class="story-form" novalidate>
          <fieldset>
            <legend>Detail Cerita</legend>
            <div class="form-group">
              <label for="description">Deskripsi <span aria-hidden="true">*</span></label>
              <textarea id="description" name="description" rows="5" required aria-required="true" placeholder="Tuliskan deskripsi cerita Anda"></textarea>
              <p class="error-text" id="error-description" aria-live="polite"></p>
            </div>
            
            <div class="form-group file-group">
              <label for="photo">Unggah Foto <span aria-hidden="true">*</span></label>
              <div class="photo-options">
                <button type="button" id="camera-button" class="form-button" aria-label="Ambil foto menggunakan kamera perangkat"> Ambil Foto</button>
                <label for="photo" class="form-button" role="button" tabindex="0"> Pilih File</label>
                <input type="file" id="photo" name="photo" accept="image/*" required aria-required="true" style="display: none;">
              </div>
              <p class="file-name-display" id="file-name-display">Belum ada file dipilih.</p>
              <p class="error-text" id="error-photo" aria-live="polite"></p>
            </div>

            <div id="photo-preview-container" class="photo-preview-container" style="display: none;">
              <img id="photo-preview" class="photo-preview" src="#" alt="Pratinjau foto yang akan diunggah" style="width: 100%; max-height: 200px; object-fit: cover;">
            </div>

            <div id="camera-stream-container" class="camera-stream-container" style="display: none;">
              <video id="camera-stream" autoplay playsinline style="width: 100%; max-height: 300px;"></video>
              <canvas id="camera-canvas" style="display: none;"></canvas>
              <button type="button" id="capture-button" class="form-button">Ambil Gambar</button>
            </div>

            <input type="hidden" id="lat" name="lat">
            <input type="hidden" id="lon" name="lon">
          </fieldset>

          <fieldset class="map-field">
            <legend>Pilih Lokasi (Opsional)</legend>
            <div id="map-picker" class="map-container" tabindex="0" role="region" aria-label="Peta untuk memilih lokasi cerita"></div>
            <p id="location-display" class="location-display">Lokasi: Belum dipilih</p>
          </fieldset>

          <button type="submit" id="submit-button" class="form-button primary-button">Kirim Story</button>
        </form>
      </div>
    `;
  },

  async afterRender() {
    this._initViewElements();
    this._initMap();
    this._setupFileSelection();
    this._setupCameraFeature(); // Kriteria 3: Advance (+4 pts)
    this._presenter = new AddStoryPresenter({ view: this });
  },

  _initViewElements() {
    this._form = document.getElementById("addStoryForm");
    this._descriptionInput = document.getElementById("description");
    this._photoInput = document.getElementById("photo");
    this._latInput = document.getElementById("lat");
    this._lonInput = document.getElementById("lon");
    this._mapPicker = document.getElementById("map-picker");
    this._locationDisplay = document.getElementById("location-display");
    this._loadingIndicator = document.getElementById("loading-indicator");
    this._statusMessage = document.getElementById("status-message");
    this._fileNameDisplay = document.getElementById("file-name-display");
    this._photoPreviewContainer = document.getElementById(
      "photo-preview-container"
    );
    this._photoPreview = document.getElementById("photo-preview");

    // Elemen Kamera
    this._cameraButton = document.getElementById("camera-button");
    this._cameraStreamContainer = document.getElementById(
      "camera-stream-container"
    );
    this._cameraStream = document.getElementById("camera-stream");
    this._cameraCanvas = document.getElementById("camera-canvas");
    this._captureButton = document.getElementById("capture-button");
    this._mediaStream = null;
    this._capturedFile = null; // Menyimpan file yang diambil dari kamera
  },

  // --- Kriteria 3: Pemilihan lokasi dari peta (Basic +2 pts) ---
  _initMap() {
    this._map = L.map(this._mapPicker).setView([-7.257472, 112.75209], 12);
    L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
      attribution:
        '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
    }).addTo(this._map);

    this._marker = null;

    // Event klik di peta untuk memilih lokasi
    this._map.on("click", (e) => {
      const { lat, lng } = e.latlng;
      this._updateLocation(lat, lng);
    });
  },

  _updateLocation(lat, lon) {
    if (this._marker) {
      this._map.removeLayer(this._marker);
    }

    // Kriteria 3: Marker di peta saat klik
    this._marker = L.marker([lat, lon]).addTo(this._map);
    this._map.setView([lat, lon], this._map.getZoom());

    this._latInput.value = lat;
    this._lonInput.value = lon;
    this._locationDisplay.textContent = `Lokasi: Lat ${lat.toFixed(
      4
    )}, Lon ${lon.toFixed(4)}`;
  },

  // --- Kriteria 3: Upload File (Basic +2 pts) ---
  _setupFileSelection() {
    this._photoInput.addEventListener("change", () => {
      this._fileNameDisplay.textContent = this._photoInput.files[0]
        ? this._photoInput.files[0].name
        : "Belum ada file dipilih.";
      this._updatePreview(this._photoInput.files[0]);
      this._capturedFile = null; // Reset file kamera jika user memilih file dari disk
      this._closeCameraStream(); // Tutup kamera jika user memilih file
    });
  },

  _updatePreview(file) {
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this._photoPreview.src = e.target.result;
        this._photoPreviewContainer.style.display = "block";
      };
      reader.readAsDataURL(file);
    } else {
      this._photoPreviewContainer.style.display = "none";
    }
  },

  // --- Kriteria 3: Camera (Advance +4 pts) ---
  _setupCameraFeature() {
    this._cameraButton.addEventListener("click", async () => {
      if (this._mediaStream) {
        this._closeCameraStream();
      } else {
        await this._openCameraStream();
      }
    });

    this._captureButton.addEventListener("click", () => {
      this._capturePhoto();
    });
  },

  async _openCameraStream() {
    try {
      this._mediaStream = await navigator.mediaDevices.getUserMedia({
        video: true,
      });
      this._cameraStream.srcObject = this._mediaStream;
      this._cameraStreamContainer.style.display = "block";
      this._photoInput.value = ""; // Reset input file
      this._fileNameDisplay.textContent = "Mengambil gambar dari kamera...";
    } catch (error) {
      this.showErrorMessage(
        "Gagal mengakses kamera. Pastikan izin kamera diberikan."
      );
      console.error("Camera error:", error);
    }
  },

  _closeCameraStream() {
    if (this._mediaStream) {
      this._mediaStream.getTracks().forEach((track) => track.stop());
      this._mediaStream = null;
    }
    this._cameraStreamContainer.style.display = "none";
  },

  _capturePhoto() {
    if (!this._mediaStream) return;

    const context = this._cameraCanvas.getContext("2d");
    this._cameraCanvas.width = this._cameraStream.videoWidth;
    this._cameraCanvas.height = this._cameraStream.videoHeight;
    context.drawImage(
      this._cameraStream,
      0,
      0,
      this._cameraCanvas.width,
      this._cameraCanvas.height
    );

    // Konversi Canvas ke Blob (File)
    this._cameraCanvas.toBlob((blob) => {
      this._capturedFile = new File(
        [blob],
        `captured-photo-${Date.now()}.png`,
        { type: "image/png" }
      );
      this._fileNameDisplay.textContent = this._capturedFile.name;
      this._updatePreview(this._capturedFile);
      this._closeCameraStream();
      this.showMessage("Gambar berhasil diambil dari kamera!", "success");
    }, "image/png");
  },

  // --- Kriteria 3: Interaktivitas Form (Skilled +3 pts) ---
  _validateInput(inputElement) {
    const errorElement = document.getElementById(`error-${inputElement.id}`);
    if (inputElement.validity.valueMissing) {
      errorElement.textContent = `${inputElement.name} tidak boleh kosong.`;
      inputElement.classList.add("is-invalid");
      return false;
    }
    errorElement.textContent = "";
    inputElement.classList.remove("is-invalid");
    return true;
  },

  _validateForm() {
    const isDescriptionValid = this._validateInput(this._descriptionInput);

    // Cek file, baik dari input file atau dari kamera
    const hasPhoto = this._photoInput.files.length > 0 || !!this._capturedFile;
    const errorPhoto = document.getElementById("error-photo");
    if (!hasPhoto) {
      errorPhoto.textContent = "Foto wajib diunggah.";
      return false;
    }
    errorPhoto.textContent = "";

    return isDescriptionValid && hasPhoto;
  },

  // View Method untuk Presenter
  setFormSubmitHandler(handler) {
    this._form.addEventListener("submit", (e) => {
      e.preventDefault();

      // Kriteria 3: Validasi Input
      if (!this._validateForm()) {
        this.showErrorMessage(
          "Terdapat kesalahan pada form. Mohon periksa kembali."
        );
        return;
      }

      const formData = {
        description: this._descriptionInput.value,
        photo: this._capturedFile || this._photoInput.files[0],
        lat: this._latInput.value
          ? parseFloat(this._latInput.value)
          : undefined,
        lon: this._lonInput.value
          ? parseFloat(this._lonInput.value)
          : undefined,
      };

      handler(formData);
    });
  },

  showLoading() {
    this._loadingIndicator.style.display = "block";
    this._statusMessage.style.display = "none";
    document.getElementById("submit-button").disabled = true;
  },

  hideLoading() {
    this._loadingIndicator.style.display = "none";
    document.getElementById("submit-button").disabled = false;
  },

  showMessage(message, type = "info") {
    this._statusMessage.textContent = message;
    this._statusMessage.className = `status-message ${type}`; // Menggunakan CSS class untuk styling
    this._statusMessage.style.display = "block";
  },

  showSuccessMessage(message) {
    this.showMessage(message, "success");
  },

  showErrorMessage(message) {
    this.showMessage(message, "error");
  },
};

export default AddStoryPage;
