import StoryAPI from "../data/story-api";
import Auth from "../utils/auth";
import routes from "../routes/routes";

class LoginPresenter {
  constructor({ view }) {
    this._view = view;
    this._view.setFormSubmitHandler(this._handleSubmit.bind(this));
  }

  async _handleSubmit(formData) {
    this._view.showLoading();

    try {
      const { email, password } = formData; // Pastikan kedua field tidak kosong

      if (!email || !password) {
        throw new Error("Email dan password harus diisi.");
      }

      const { token } = await StoryAPI.login({ email, password }); // Menyimpan token

      Auth.setAuthToken(token);

      this._view.showSuccessMessage(
        "Login berhasil! Mengalihkan ke halaman utama..."
      ); // Redirect ke halaman utama

      setTimeout(() => {
        window.location.hash = routes.HOME.url;
      }, 1000);
    } catch (error) {
      // Tangani error: Tampilkan pesan spesifik dari error atau pesan default yang lebih informatif
      const errorMessage =
        error.message || "Login gagal. Silakan cek koneksi atau detail Anda.";
      this._view.showErrorMessage(errorMessage);
      console.error("Login Error:", error);
    } finally {
      this._view.hideLoading();
    }
  }
}

export default LoginPresenter;
