/**
 * Utility untuk menambahkan validasi dasar pada form HTML5.
 */
const FormValidator = {
  /**
   * Menginisialisasi validasi untuk form tertentu.
   * @param {HTMLFormElement} formElement - Elemen form yang akan divalidasi.
   */
  init(formElement) {
    if (!formElement) {
      console.error("Form element tidak ditemukan.");
      return;
    }

    // Nonaktifkan validasi default browser untuk menggunakan validasi custom
    formElement.setAttribute("novalidate", true);

    // Tambahkan event listener untuk validasi saat user mengetik
    formElement.addEventListener("input", (e) => {
      this._validateInput(e.target);
    });

    // Tambahkan event listener untuk validasi saat user mencoba submit
    formElement.addEventListener("submit", (e) => {
      if (!this._validateForm(formElement)) {
        e.preventDefault(); // Cegah submit jika ada input yang tidak valid
      }
    });

    // Jalankan validasi awal untuk menampilkan error jika ada field yang sudah terisi dan tidak valid
    Array.from(formElement.elements).forEach((input) => {
      if (input.willValidate && input.value) {
        this._validateInput(input);
      }
    });
  },

  /**
   * Memvalidasi satu input field.
   * @param {HTMLInputElement|HTMLTextAreaElement} input - Elemen input yang divalidasi.
   */
  _validateInput(input) {
    if (!input.willValidate) return;

    const isValid = input.checkValidity();
    const errorMessageElement = document.querySelector(
      `[data-for="${input.id}"]`
    );

    if (isValid) {
      input.classList.remove("is-invalid");
      input.classList.add("is-valid");
      if (errorMessageElement) {
        errorMessageElement.textContent = "";
      }
    } else {
      input.classList.add("is-invalid");
      input.classList.remove("is-valid");
      if (errorMessageElement) {
        // Tampilkan pesan error bawaan browser atau pesan kustom
        errorMessageElement.textContent = this._getErrorMessage(input);
      }
    }
  },

  /**
   * Mendapatkan pesan error yang relevan untuk input field.
   * @param {HTMLInputElement|HTMLTextAreaElement} input - Elemen input.
   * @returns {string} Pesan error.
   */
  _getErrorMessage(input) {
    if (input.validity.valueMissing) {
      return `Bidang ${input.id} wajib diisi.`;
    }
    if (input.validity.typeMismatch) {
      if (input.type === "email") return "Format email tidak valid.";
    }
    if (input.validity.tooShort) {
      return `Password minimal ${input.minLength} karakter.`;
    }
    // Pesan error default jika tidak ada kondisi yang cocok
    return input.validationMessage;
  },

  /**
   * Memvalidasi seluruh form.
   * @param {HTMLFormElement} formElement - Elemen form.
   * @returns {boolean} True jika seluruh form valid.
   */
  _validateForm(formElement) {
    let isFormValid = true;
    Array.from(formElement.elements).forEach((input) => {
      this._validateInput(input);
      if (input.willValidate && !input.checkValidity()) {
        isFormValid = false;
      }
    });
    return isFormValid;
  },
};

export default FormValidator;
