const AUTH_TOKEN_KEY = "authToken";

const Auth = {
  /**
   * Menyimpan token otentikasi ke localStorage.
   * @param {string} token
   */
  setAuthToken(token) {
    if (typeof token !== "string" || token.trim() === "") {
      console.error("Token tidak valid.");
      return;
    }
    localStorage.setItem(AUTH_TOKEN_KEY, token);
  },

  /**
   * Mengambil token otentikasi dari localStorage.
   * @returns {string | null} Token atau null jika tidak ada.
   */
  getAuthToken() {
    return localStorage.getItem(AUTH_TOKEN_KEY);
  },

  /**
   * Menghapus token otentikasi dari localStorage.
   */
  clearAuthToken() {
    localStorage.removeItem(AUTH_TOKEN_KEY);
  },

  /**
   * Memeriksa apakah pengguna sudah login (token tersedia).
   * @returns {boolean}
   */
  isLoggedIn() {
    const token = this.getAuthToken();
    // Memastikan token ada dan bukan string kosong
    return !!token;
  },
};

export default Auth;
