import Auth from "./auth";
import routes from "../routes/routes";

const checkUserAuth = {
  // Panggil di beforeRender pages Login/Register
  checkAndRedirectAuth() {
    if (Auth.isLoggedIn()) {
      window.location.hash = routes.HOME.url; // Mengembalikan false untuk memberi tahu router agar menghentikan rendering
      return false;
    }
    return true; // Lanjutkan rendering jika tidak perlu redirect
  }, // Panggil di beforeRender pages yang memerlukan auth (seperti Home/Add Story)

  checkAndRedirectGuest() {
    if (!Auth.isLoggedIn()) {
      window.location.hash = routes.LOGIN.url; // Mengembalikan false untuk memberi tahu router agar menghentikan rendering
      return false;
    }
    return true; // Lanjutkan rendering jika user sudah login
  },
};

export default checkUserAuth;
