// PERBAIKAN 1: Hapus import StoryApi dari sini.
// Presenter tidak perlu tahu implementasi API-nya.

class HomePresenter {
  // PERBAIKAN 2: Terima 'api' sebagai parameter di constructor
  constructor({ view, api }) {
    this._view = view;
    // PERBAIKAN 3: Simpan 'api' yang di-inject (diberikan)
    this._api = api; 
    this._stories = [];

    // Memberikan referensi presenter ke view
    this._view.setPresenter(this);

    // Langsung muat daftar cerita saat inisialisasi
    this._loadStories();
  }

  /**
   * Mengambil data cerita dari API
   */
  async _loadStories() {
    this._view.showLoading();
    try {
      // Panggilan ini sekarang akan berhasil karena this._api merujuk ke
      // StoryAPI (static class) yang kita berikan dari home-page.js
      const response = await this._api.getStories();

      // Mengambil listStory dengan aman.
      const storyList = response.listStory || [];

      this._stories = storyList;
      this._view.renderStories(this._stories);
      this._view.renderMap(this._stories);
      this._view.hideLoading();
    } catch (error) {
      this._view.hideLoading();

      // Menampilkan pesan error spesifik yang ditangkap dari StoryAPI
      this._view.showErrorMessage(
        `Gagal memuat cerita: ${error.message}. Pastikan Anda telah menginstal 'leaflet' dan API berjalan.`
      );

      console.error("Error saat memuat cerita:", error);
    }
  }

  /**
   * Memindahkan peta ke lokasi cerita tertentu dan menyorotnya di peta/list.
   * Dipanggil oleh event listener di View.
   * @param {number} lat - Latitude
   * @param {number} lon - Longitude
   */
  centerMapToStory(lat, lon) {
    // Zoom diatur lebih detail agar fokus ke lokasi
    this._view.setMapView(lat, lon, 15);
  }

  /**
   * Menyorot cerita di list dan membuka popup di peta
   * Dipanggil oleh event listener di View.
   * @param {string} storyId - ID cerita
   */
  highlightStory(storyId) {
    this._view.highlightStory(storyId);
  }

  /**
   * Mengambil hanya cerita yang memiliki koordinat lokasi
   * @returns {Array<Object>} Daftar cerita dengan lat dan lon
   */
  getStoriesWithLocation() {
    return this._stories.filter((story) => story.lat && story.lon);
  }
}

export default HomePresenter;