import RegisterPresenter from "../../../presenter/register-presenter";
import StoryAPI from "../../../data/story-api"; // Perbaikan Path
import Auth from "../../../utils/auth"; // Perbaikan Path
import routes from "../../../routes/routes"; // Perbaikan Path
import checkUserAuth from "../../../utils/check-user-auth";
import FormValidator from "../../../utils/form-validator";

const RegisterPage = {
  // beforeRender dipanggil sebelum page.render()
  async beforeRender() {
    // Kriteria 5: Pengecekan autorisasi
    // Jika user sudah login, redirect ke Home dan hentikan rendering
    return checkUserAuth.checkAndRedirectAuth();
  },

  async render() {
    return `
      <div class="auth-container">
        <h2>Daftar Akun Baru</h2>
        <form id="register-form">
          <div class="form-group">
            <label for="name">Nama Lengkap:</label>
            <input type="text" id="name" required>
            <div class="invalid-feedback" data-for="name"></div>
          </div>
          <div class="form-group">
            <label for="email">Email:</label>
            <input type="email" id="email" required>
            <div class="invalid-feedback" data-for="email"></div>
          </div>
          <div class="form-group">
            <label for="password">Password:</label>
            <input type="password" id="password" required minlength="8">
            <div class="invalid-feedback" data-for="password"></div>
          </div>
          <button type="submit" id="register-submit">Daftar</button>
          <div id="loading-indicator" class="loading-indicator"></div>
          <div id="message-box" class="message-box"></div>
        </form>
        <p>Sudah punya akun? <a href="${routes.LOGIN.url}">Login di sini</a></p>
      </div>
    `;
  },

  async afterRender() {
    const registerForm = document.getElementById("register-form");
    const loadingIndicator = document.getElementById("loading-indicator");
    const messageBox = document.getElementById("message-box");

    // Implementasi View Interface untuk Presenter Register
    const view = {
      setFormSubmitHandler: (handler) => {
        registerForm.addEventListener("submit", (e) => {
          e.preventDefault();
          const name = document.getElementById("name").value;
          const email = document.getElementById("email").value;
          const password = document.getElementById("password").value;
          handler({ name, email, password }); // Mengirim 3 data
        });
      },
      showLoading: () => {
        loadingIndicator.textContent = "Memproses...";
        loadingIndicator.style.display = "block";
        messageBox.style.display = "none";
      },
      hideLoading: () => {
        loadingIndicator.style.display = "none";
      },
      showSuccessMessage: (message) => {
        messageBox.textContent = message;
        messageBox.className = "message-box success";
        messageBox.style.display = "block";
      },
      showErrorMessage: (message) => {
        messageBox.textContent = message;
        messageBox.className = "message-box error";
        messageBox.style.display = "block";
      },
    };

    // Inisialisasi RegisterPresenter
    new RegisterPresenter({ view });

    // Tambahkan validasi form dasar
    FormValidator.init(registerForm);
  },
};

export default RegisterPage;
