import CONFIG from "../utils/config";
import Auth from "../utils/auth";

class StoryAPI {
  /**
   * Handler untuk memproses respons fetch.
   */
  static async _handleResponse(response) {
    if (!response.ok) {
      try {
        const errorJson = await response.json();
        throw new Error(
          errorJson.message ||
            `Gagal menghubungi API: ${response.status} ${response.statusText}`
        );
      } catch {
        throw new Error(
          `Gagal menghubungi API: ${response.status} ${response.statusText}`
        );
      }
    }

    const responseJson = await response.json();

    if (responseJson.error) {
      throw new Error(responseJson.message);
    }

    return responseJson;
  }

  // ==============================
  // ============ AUTH ============
  // ==============================

  static async register({ name, email, password }) {
    const response = await fetch(`${CONFIG.STORY_API_BASE_URL}/register`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name, email, password }),
    });

    return StoryAPI._handleResponse(response);
  }

  static async login({ email, password }) {
    const response = await fetch(`${CONFIG.STORY_API_BASE_URL}/login`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, password }),
    });

    const data = await StoryAPI._handleResponse(response);

    if (data.loginResult?.token) {
      return { token: data.loginResult.token };
    }

    throw new Error("Login berhasil, tetapi token tidak ditemukan.");
  }

  // ==============================
  // =========== STORY ============
  // ==============================

  static async getStories({ page = 1, size = 10, location = 0 } = {}) {
    const token = Auth.getAuthToken();

    const url = new URL(`${CONFIG.STORY_API_BASE_URL}/stories`);
    url.searchParams.set("page", page);
    url.searchParams.set("size", size);
    url.searchParams.set("location", location);

    const headers = {};
    if (token) headers.Authorization = `Bearer ${token}`;

    const response = await fetch(url, { headers });
    return StoryAPI._handleResponse(response);
  }

  /**
   *  PERBAIKAN: standard Dicoding  createStory
   */
  static async createStory({ description, photo, lat, lon }) {
    const token = Auth.getAuthToken();

    const formData = new FormData();
    formData.append("description", description);
    formData.append("photo", photo);
    if (lat) formData.append("lat", lat);
    if (lon) formData.append("lon", lon);

    const headers = {};
    if (token) headers.Authorization = `Bearer ${token}`;

    const response = await fetch(`${CONFIG.STORY_API_BASE_URL}/stories`, {
      method: "POST",
      headers,
      body: formData,
    });

    return StoryAPI._handleResponse(response);
  }
}

export default StoryAPI;
