import StoryAPI from "../data/story-api";
import IDBHelper from "../utils/idb-helper";

class AddStoryPresenter {
  constructor({ view }) {
    this._view = view;
    this._view.setFormSubmitHandler(this._addStory.bind(this));

    // Sync otomatis saat online
    window.addEventListener("online", () => {
      this._syncPendingStories();
    });

    // Coba sync saat halaman dibuka
    if (navigator.onLine) this._syncPendingStories();
  }

  async _addStory(formData) {
    this._view.showLoading();

    try {
      const { description, photo, lat, lon } = formData;

      if (!description || !photo) {
        throw new Error("Deskripsi dan Gambar wajib diisi.");
      }

      // === MODE OFFLINE ===
      if (!navigator.onLine) {
        const pendingPayload = {
          id: Date.now(), //  menjadi key untuk IDB
          description,
          photo,
          lat,
          lon,
          createdAt: Date.now(),
        };

        await IDBHelper.addPendingStory(pendingPayload);

        this._view.showInfoMessage(
          "Anda offline. Cerita disimpan dan akan diunggah otomatis saat online."
        );

        this._view.hideLoading();
        setTimeout(() => (window.location.hash = "#/"), 1200);
        return;
      }

      // === MODE ONLINE ===
      await StoryAPI.createStory({ description, photo, lat, lon });
      this._view.showSuccessMessage("Cerita berhasil ditambahkan.");

      setTimeout(() => {
        window.location.hash = "#/";
      }, 1500);
    } catch (error) {
      console.error(error);
      this._view.showErrorMessage(`Gagal menambahkan cerita: ${error.message}`);
    } finally {
      this._view.hideLoading();
    }
  }

  async _syncPendingStories() {
    try {
      const pendings = await IDBHelper.getAllPendingStories();
      if (!pendings || pendings.length === 0) return;

      for (const pendingStory of pendings) {
        try {
          await StoryAPI.createStory({
            description: pendingStory.description,
            photo: pendingStory.photo,
            lat: pendingStory.lat,
            lon: pendingStory.lon,
          });

          //  gunakan ID yang benar untuk hapus pending
          await IDBHelper.deletePendingStory(pendingStory.id);
        } catch (err) {
          console.warn("Gagal upload story offline:", pendingStory, err);
        }
      }
    } catch (err) {
      console.error("Gagal sync story offline:", err);
    }
  }
}

export default AddStoryPresenter;
