import "regenerator-runtime"; // untuk async/await transpile
import "../styles/styles.css";
import App from "./app";

// Inisialisasi aplikasi setelah DOM siap
document.addEventListener("DOMContentLoaded", () => {
  const app = new App({
    button: document.querySelector("#drawer-button"),
    drawer: document.querySelector("#navigation-drawer"),
    content: document.querySelector("#main-content"),
  });

  // Jalankan render awal aplikasi
  app.renderPage();
});

//  Registrasi Service Worker (pakai path relatif untuk GitHub Pages)
if ("serviceWorker" in navigator) {
  window.addEventListener("load", () => {
    navigator.serviceWorker
      .register("./service-worker.js") // penting: gunakan path relatif!
      .then((registration) => {
        console.log(" Service Worker terdaftar:", registration.scope);
      })
      .catch((error) => {
        console.error(" Gagal mendaftar Service Worker:", error);
      });
  });
}
