// Import Presenter, Config, dan (BARU) API Service
import HomePresenter from "../../presenter/home-presenter";
import CONFIG from "../../utils/config";
import StoryAPI from "../../data/story-api"; // <-- PERBAIKAN 1: Impor API Service
import L from "leaflet";

// Hapus default marker icon jika menggunakan webpack
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
});

const HomePage = {
  async render() {
    return `
      <h2 class="page-title">Beranda Story Kami</h2>
      <p class="skip-content-anchor" id="skip-to-content"></p> 
      <div id="loading-indicator" class="loading-indicator" style="display: none;">Memuat data...</div>
      <div id="error-message" class="error-message" style="display: none;"></div>
      <section class="story-layout container">
        <div class="story-list-container">
          <h3>Daftar Story</h3>
          <a href="#/add-story" class="button-add-story" aria-label="Tambah Story Baru">
             Tambah Story
          </a>
          <ul id="story-list" class="story-list" role="list">
            </ul>
        </div>
        <div class="story-map-container">
          <h3>Peta Lokasi Story</h3>
          <div id="map" class="map-container" tabindex="0" role="region" aria-label="Peta lokasi cerita"></div>
          
          <div class="map-layer-control">
            <h4>Pilih Tampilan Peta</h4>
            <label>
              <input type="radio" name="map-layer" value="osm" checked> OpenStreetMap
            </label>
            <label>
              <input type="radio" name="map-layer" value="mapbox"> Mapbox Street (Membutuhkan API Key)
            </label>
          </div>
        </div>
      </section>
    `;
  },

  async afterRender() {
    this._initViewElements();
    
    // PERBAIKAN 2: Inisialisasi Presenter dengan View dan API
    this._presenter = new HomePresenter({
      view: this,
      api: StoryAPI, // <-- Berikan class API yang sudah static
    });

    this._setupLayerControl();
  },

  // Metode untuk menerima Presenter (bagian dari View Interface)
  setPresenter(presenter) {
    this._presenter = presenter;
  },

  _initViewElements() {
    this._mainContent = document.getElementById("main-content");
    this._loadingIndicator = document.getElementById("loading-indicator");
    this._errorMessage = document.getElementById("error-message");
    this._storyListElement = document.getElementById("story-list");
    this._mapElement = document.getElementById("map");
    this._map = null;
    this._markers = [];
  },

  // --- View Methods (Dipanggil oleh Presenter) ---
  showLoading() {
    this._loadingIndicator.style.display = "block";
    this._errorMessage.style.display = "none";
  },

  hideLoading() {
    this._loadingIndicator.style.display = "none";
  },

  showErrorMessage(message) {
    this._errorMessage.textContent = message;
    this._errorMessage.style.display = "block";
  },

  renderStories(stories) {
    this._storyListElement.innerHTML = ""; // Clear existing list
    stories.forEach((story) => {
      const storyItem = document.createElement("li");
      storyItem.className = "story-item";
      storyItem.setAttribute("tabindex", "0");
      storyItem.setAttribute("data-id", story.id);

      // Event listener sekarang memanggil Presenter
      storyItem.addEventListener("click", () => {
        if (story.lat && story.lon) {
          // Hanya jika story memiliki lokasi, panggil Presenter
          this._presenter.highlightStory(story.id);
          this._presenter.centerMapToStory(story.lat, story.lon);
        }
      });

      const photoAltText = `Foto yang diunggah oleh ${
        story.name
      } dengan deskripsi: ${story.description.substring(0, 50)}...`;

      storyItem.innerHTML = `
        <article class="story-card">
          <img src="${story.photoUrl}" alt="${photoAltText}" class="story-img">
          <div class="story-content">
            <h4 class="story-name">${story.name}</h4>
            <p class="story-description">${story.description.substring(
              0,
              100
            )}...</p>
            <time class="story-date" datetime="${story.createdAt}">${new Date(
        story.createdAt
      ).toLocaleDateString()}</time>
            ${
              story.lat && story.lon
                ? '<span class="story-location"> Lokasi Tersedia</span>'
                : ""
            }
          </div>
        </article>
      `;
      this._storyListElement.appendChild(storyItem);
    });
  },

  // --- Kriteria 2: Visualisasi Peta (Map View Logic) ---
  _setupLayerControl() {
    const radioButtons = document.querySelectorAll('input[name="map-layer"]');
    radioButtons.forEach((radio) => {
      radio.addEventListener("change", (event) => {
        const selectedLayer = event.target.value;
        this._changeMapLayer(selectedLayer);
      });
    });
  },

  _baseLayer: null,
  _mapboxLayer: null,
  _osmLayer: null,
  _layerControl: null,

  _createMapLayers() {
    // OpenStreetMap Default Layer
    this._osmLayer = L.tileLayer(
      "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      {
        attribution:
          '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      }
    );

    // Mapbox Layer (Contoh untuk Advance +4)
    this._mapboxLayer = L.tileLayer(
      `https://api.mapbox.com/styles/v1/mapbox/streets-v11/tiles/{z}/{x}/{y}?access_token=${CONFIG.MAPBOX_ACCESS_TOKEN}`,
      {
        attribution:
          'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>',
        tileSize: 512,
        zoomOffset: -1,
      }
    );

    this._baseLayers = {
      OpenStreetMap: this._osmLayer,
      "Mapbox Street": this._mapboxLayer,
    };
  },

  _changeMapLayer(layerName) {
    if (this._map) {
      // Remove all current base layers
      Object.values(this._baseLayers).forEach((layer) => {
        if (this._map.hasLayer(layer)) {
          this._map.removeLayer(layer);
        }
      });

      // Add selected layer
      if (layerName === "osm") {
        this._map.addLayer(this._baseLayers["OpenStreetMap"]);
      } else if (layerName === "mapbox") {
        this._map.addLayer(this._baseLayers["Mapbox Street"]);
      }
    }
  },

  renderMap(stories) {
    if (this._map) {
      this._map.remove(); // Hapus peta lama jika ada
    }

    // Inisialisasi peta
    this._map = L.map(this._mapElement).setView(
      [CONFIG.DEFAULT_MAP_LATITUDE, CONFIG.DEFAULT_MAP_LONGITUDE],
      CONFIG.DEFAULT_MAP_ZOOM
    );
    this._createMapLayers();
    this._osmLayer.addTo(this._map); // Default layer

    // Tambahkan kontrol layer
    this._layerControl = L.control.layers(this._baseLayers).addTo(this._map);

    this._markers = stories
      .filter((story) => story.lat && story.lon) // Filter cerita yang memiliki lokasi
      .map((story) => {
        // Kriteria 2: Marker dan Pop-up
        const marker = L.marker([story.lat, story.lon], { id: story.id })
          .bindPopup(`
            <strong>${story.name}</strong><br>
            ${story.description.substring(0, 50)}...
            <br>
            <img src="${story.photoUrl}" alt="Gambar Story ${
              story.name
            }" style="width: 100px; height: auto;">
          `);

        // Event listener marker sekarang memanggil Presenter
        marker.on("click", () => {
          this._presenter.highlightStory(story.id);
        });

        return marker;
      });

    // Tambahkan semua marker ke peta
    const markerGroup = L.featureGroup(this._markers);
    markerGroup.addTo(this._map);

    if (this._markers.length > 0) {
      this._map.fitBounds(markerGroup.getBounds());
    }

    // Pastikan peta dapat dioperasikan dengan keyboard (Kriteria 4)
    this._mapElement.setAttribute("tabindex", "0");
  },

  // --- View Methods untuk Interaksi Peta (Dipanggil oleh Presenter) ---
  highlightStory(storyId) {
    this._markers.forEach((marker) => {
      // Logic untuk membuka popup di peta
      if (marker.options.id === storyId) {
        marker.openPopup();
      }
    });

    // Sinkronisasi list (Highlight item di list)
    document.querySelectorAll(".story-item").forEach((item) => {
      item.classList.remove("highlighted");
      if (item.dataset.id === storyId) {
        item.classList.add("highlighted");
        item.focus(); // Fokus ke elemen list untuk aksesibilitas keyboard
      }
    });
  },

  setMapView(lat, lon, zoom) {
    if (this._map) {
      this._map.setView([lat, lon], zoom);
    }
  },
};

export default HomePage;